html, body {
	width: 100vw;
	height: 100vh;
	margin: 0;
	padding: 0; }


nav {
	position: relative;
	ul {
		list-style: none;
		display: flex;
		flex-direction: row;
		li {
			margin: 0 1rem;
			a {
				color: #000;
				text-decoration: none; } } }
	span {
		position: absolute;
		bottom: 0;
		left: 10px;
		height: 2px;
		width: 20px;
		transition: width 230ms ease, left 230ms ease;
		background: #000; } }
